import React, { useEffect, useRef, useState } from "react";
import Tables from './components/Tables'
import DropdownButton from 'react-bootstrap/DropdownButton';
import Tabletop from "tabletop";
import "bootstrap/dist/css/bootstrap.min.css";
import "./shards-dashboard/styles/shards-dashboards.1.1.0.min.css";
import CollapsibleTable from "./components/CollapsibleTable.js"
import logo from './components/pk.png'

import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  CardFooter,
  InputGroupText,
  InputGroupAddon,
  Badge,
  Button,

  InputGroup,
  FormInput,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Progress,
  Alert

} from "shards-react";
import SubTables from "./components/SubTables";

function App() {
  const [isLoading, setLoading] = React.useState(true);
  const [isLoadingSub, setLoadingSub] = React.useState(true);
  const [googleData, setGoogleData] = React.useState(null);
  const [data, setData] = React.useState(null);
  const [deptData, setDeptData] = React.useState(null);
  const [dropdown1, setdropdown1] = React.useState(false);
  const [stateA, setSState] = React.useState(false);
  const [selected, setSelected] = React.useState("");
  const [selectedName, setSelectedName] = React.useState("");
  const [selectedIndex, setSelectedIndex] = React.useState(9);
  const [showData, setShowData] = React.useState(null);

  const toggle = (which) => {
    const newState = { ...stateA };
    newState[which] = !stateA[which];
    setSState(newState);
    console.log(newState);
    setdropdown1(!dropdown1)
    setLoadingSub(true);
  }
  const handleChange = (event) => {
    setSelected(event);
    setLoadingSub(true);
  }

  const setName = (event) => {
    setLoadingSub(true);
    setSelected(deptData[event]);
    setSelectedIndex(event);
  }

  const showSelectedData = (event) => {
    setSelectedName(event);
    var values = [];
    Tabletop.init({
      key: "10ebCbnOABBsTsV7U9DfAGF-gxCc6ewkBxErVRsgt0_s",
      callback: (googleData) => {
        console.log("google sheet data --->", googleData);
        googleData.map((obj, index) => {
          if (obj.teacher == event) {
            values.push(obj);
          }
        });

        setShowData(values);
        setLoadingSub(false);

        console.log(values);
      },
      simpleSheet: true,
    });
  }

  useEffect(() => {
    Tabletop.init({
      key: "13KkkMk2pnHmS3WFCgXYtQ_yvbyOa0UuG2Za8P2nQI8s",
      callback: (googleData) => {
        console.log("google sheet data --->", googleData);
        var category = []; //แสดงกิจกรรมหน้าแรก
        var i = 0;
        var data = [];
        var depts = [];
        var itemThai = [];
        var itemMath = [];
        var itemST = [];
        var itemSocial = [];
        var itemPS = [];
        var itemHome = [];
        var itemArt = [];
        var itemEng = [];
        var itemNa = [];
        googleData.map((obj, index) => {
          data.push(obj);
          depts.push(obj.dept);
          if (obj.dept == "ภาษาไทย") {
            itemThai.push(obj);
          }
          else if (obj.dept == "คณิตศาสตร์") {
            itemMath.push(obj);
          }
          else if (obj.dept == "วิทยาศาสตร์และเทคโนโลยี") {
            itemST.push(obj);
          }
          else if (obj.dept == "สังคมศึกษาฯ") {
            itemSocial.push(obj);
          }
          else if (obj.dept == "สุขศึกษาและพลศึกษา") {
            itemPS.push(obj);
          }
          else if (obj.dept == "ศิลปะ") {
            itemArt.push(obj);
          }
          else if (obj.dept == "การงานอาชีพ") {
            itemHome.push(obj);
          }
          else if (obj.dept == "ภาษาต่างประเทศ") {
            itemEng.push(obj);
          }
          else {
            itemNa.push(obj);
          }
        });
        let dup = [...new Set(depts)];
        setDeptData(dup);
        console.log(dup);

        var a = [];
        a.push(itemThai);
        a.push(itemMath);
        a.push(itemST);
        a.push(itemSocial);
        a.push(itemPS);
        a.push(itemArt);
        a.push(itemHome);
        a.push(itemEng);
        a.push(itemNa);
        a.push([]);
        setGoogleData(data);
        setData(a);
        setLoading(false);
      },
      simpleSheet: true,
    });
  }, []);

  return (
    <Container fluid style={{ fontFamily: "Sarabun" }}>{
      !isLoading ? (
        <Row className="mb-1" key="r0">
          <Col lg="12" sm="12" className="mb-3" style={{ textAlign: "center" }}>
            <img src={logo} style={{ width: "10vw" }}></img>
            <h3 style={{ fontFamily: "Sarabun" }}>ระบบตรวจสอบรายงานการสอนออนไลน์ โรงเรียนภูเขียว</h3>

            <InputGroup className="mb-4" style={{ paddingLeft: "40px", paddingRight: "40px" }}>
              <FormInput id="tname" value={selected} style={{ fontSize: "16px" }} disabled onChange={handleChange} />
              <Dropdown
                open={dropdown1}
                toggle={() => toggle("dropdown1")}
                addonType="append"
              >
                <DropdownToggle caret><span style={{ fontSize: "18px", fontFamily: "Sarabun" }}>เลือกกลุ่มสาระฯ</span></DropdownToggle>
                <DropdownMenu right>
                  {deptData.map((obj, index) => (
                    <DropdownItem key={obj} onClick={() => setName(index)}><span style={{ fontSize: "18px" }}>{obj}</span></DropdownItem>
                  ))}
                </DropdownMenu>
              </Dropdown>
            </InputGroup>
            {selectedIndex < 9 && (
              selectedIndex < 8 ? (
                <Tables showSelectedData={showSelectedData} classText="m-0 text-white" classColor="border-bottom bg-info" title={"กลุ่มสาระการเรียนรู้" + deptData[selectedIndex]} data={data[selectedIndex]}></Tables>
              ) : (
                <Tables showSelectedData={showSelectedData} classText="m-0 text-white" classColor="border-bottom bg-info" title={"กลุ่มงาน" + deptData[selectedIndex] + "การสอน"} data={data[selectedIndex]}></Tables>
              )
            )}
            {!isLoadingSub && (
              <SubTables showSelectedData={showSelectedData} classText="m-0 text-white" classColor="border-bottom bg-success" title={"รายงานการสอนของครู " + selectedName} data={showData}></SubTables>
            )}
          </Col>
        </Row>
      ) : (
        <Row className="mb-1" key="r0">
          <Col lg="12" sm="12" className="mb-3" style={{ textAlign: "center" }}>
            <img src={logo} style={{ width: "10vw" }}></img>
            <h3 style={{ fontFamily: "Sarabun" }}>ระบบตรวจสอบรายงานการสอนออนไลน์ โรงเรียนภูเขียว</h3>
            <Alert theme="warning" fontSize="25px">
              <span style={{ fontSize: "20px" }}>กำลังโหลดข้อมูล... -{" "} ถ้าไม่มีการเคลื่อนไหว โปรด refresh หน้านี้ครับ</span>
            </Alert>
          </Col>
        </Row>
      )
    }</Container>)

}

export default App